import wordData from './backend/wordData';
import * as Swing from 'swing';
import { AdaptiveTest } from 'adaptivetesting';
import { AbilityCorrection } from './backend/AbilityCorrection';
var AdaptivePREVICTest = /** @class */ (function () {
    function AdaptivePREVICTest() {
        var _this = this;
        this.item = null;
        //event handler for left button
        this.handleNoClick = function (e) {
            _this.UpdateTest(0);
        };
        //event handler for right button
        this.handleYesClick = function (e) {
            _this.UpdateTest(1);
        };
        //Initilization of properties
        this.counter = document.getElementById('counter');
        this.buttonNo = document.getElementById('button-no');
        this.buttonYes = document.getElementById('button-yes');
        // An instance of the Stack is used to attach event listeners.
        this.stack = Swing.Stack();
        // get html element for UL for words
        this.cards = document.getElementById('stack');
        //create adaptive test
        this.test = new AdaptiveTest();
        //load data
        this.data = JSON.parse(wordData());
        //setup item counter
        this.item_counter = 0;
        //add event listener for keys
        document.addEventListener('keydown', function (e) {
            e = e || window.event;
            if (e.key === 'ArrowLeft') {
                _this.handleNoClick(e);
            }
            else if (e.key === 'ArrowRight') {
                _this.handleYesClick(e);
            }
        });
        //add event listener for buttons (left and right)
        this.buttonNo.addEventListener('click', this.handleNoClick, { capture: true });
        this.buttonYes.addEventListener('click', this.handleYesClick, { capture: true });
        // Add event listener for when a card is thrown out of the stack.
        this.stack.on('throwout', function (e) {
            var response = e.throwDirection.description === 'LEFT' ? 0 : 1;
            _this.UpdateTest(response);
        });
        //init dialog
        this.dialog = document.getElementById("dialog");
        //get se selection
        this.getSeSelection();
        //run test for the first time
        this.RunTest();
    }
    /**
     * This function will present the user with a new item
     * and set the ability level and the standard error to their current values.
     */
    AdaptivePREVICTest.prototype.RunTest = function () {
        var _this = this;
        //load item
        this.item = this.test.GetNextItem();
        var item_index = this.data.findIndex(function (i) {
            if (i.word == _this.item.word) {
                return true;
            }
        });
        //show item to user
        var newLI = document.createElement('li');
        newLI.classList.add("".concat(this.data[item_index].pos));
        newLI.appendChild(document.createTextNode(this.data[item_index].word));
        this.cards.appendChild(newLI);
        this.stack.createCard(newLI);
        this.counter.textContent = "Bisher haben Sie ".concat(this.item_counter, " W\u00F6rter beantwortet.");
    };
    /**
     * This function is triggered by the event listeners.
     * It saves the reponse, updates the ability estimation
     * and removes the item from the user interface.
     * It also checks whether the condition to stop the test is met.
     * @param {*} response
     */
    AdaptivePREVICTest.prototype.UpdateTest = function (response) {
        var _this = this;
        //remove item from UI
        this.cards.lastElementChild.remove();
        //update test
        //add response to response pattern
        this.test.ResponsePattern.push(response);
        //add item to answered items array
        this.test.AnsweredItems.push(this.item);
        //remove item from item pool
        var item_index = this.test.Items.findIndex(function (i) {
            if (i.word == _this.item.word) {
                return true;
            }
        });
        this.test.Items.splice(item_index, 1);
        //update ability level
        var estimator = new AbilityCorrection();
        try {
            this.test.AbilityLevel = estimator.GetMaximumLikelihoodEstimation(this.test.ResponsePattern, this.test.ItemDifficulties);
        }
        catch (e) {
            console.debug(e);
        }
        //check condition
        //test will stop if desired se has been achieved or
        //all items have been presented to the participant
        if (this.test.AbilitySE > this.se_selection && this.test.Items.length != 0) {
            //continue the test
            this.item_counter++;
            this.RunTest();
        }
        else {
            this.endTest();
        }
    };
    /**
     * Prepares data, downloads blob and redirects
     * to goodbye page
     */
    AdaptivePREVICTest.prototype.endTest = function () {
        var location = document.location;
        var ID = new URL(location).searchParams.get("ID");
        // drop additional properties (item parameter CI) from answered items
        var answered_items = this.test.AnsweredItems.map(function (_a) {
            var word = _a.word, Difficulty = _a.Difficulty;
            return ({ word: word, Difficulty: Difficulty });
        });
        //save values for export
        var results_values = {
            "id": ID,
            "final_ability_level": this.test.AbilityLevel,
            "final_se": this.test.AbilitySE,
            "answered_item": answered_items,
            "response_pattern": this.test.ResponsePattern
        };
        //download test results
        this.downloadTestResults(results_values);
        //go to goodbye
        setTimeout(function () {
            return (window.location.href = "./goodbye.html");
        }, 1000);
    };
    AdaptivePREVICTest.prototype.getBlobURL = function (blob) {
        var blobUrl = URL.createObjectURL(blob);
        return blobUrl;
    };
    /**
     * Debug only
     * @param results
     */
    AdaptivePREVICTest.prototype.showDialog = function (results) {
        var _this = this;
        this.dialog.showModal();
        var yes_button = document.getElementById("yes_d");
        var no_button = document.getElementById("no_d");
        yes_button.addEventListener("click", function (e) {
            var blob = new Blob([JSON.stringify(results)], {
                type: "application/json"
            });
            var url = _this.getBlobURL(blob);
            window.location.href = url;
        });
        no_button.addEventListener("click", function (e) {
            window.location.href = "./goodbye.html";
        });
    };
    AdaptivePREVICTest.prototype.downloadTestResults = function (results) {
        var blob = new Blob([JSON.stringify(results)], { type: "application/json" });
        var blobUrl = window.URL.createObjectURL(blob);
        var day = new Date().toISOString().substr(0, 10);
        var time = new Date().toISOString().substr(11, 8);
        var location = document.location;
        var ID = new URL(location).searchParams.get("ID");
        var a = document.createElement("a");
        a.href = blobUrl;
        a.download = "previc-".concat(ID, "-").concat(day, "-").concat(time, ".json");
        a.click();
    };
    AdaptivePREVICTest.prototype.getSeSelection = function () {
        var location = document.location;
        var params = new URL(location).searchParams;
        var se_parameter = params.get("se");
        // Check if se_parameter is a valid number between 0.1 and 0.9
        if (se_parameter !== null) {
            var se_number = parseFloat(se_parameter);
            if (!isNaN(se_number) && se_number >= 0.1 && se_number <= 0.9) {
                this.se_selection = se_number;
            }
            else {
                // If se_parameter is not valid, set a default value
                this.se_selection = 0.4;
            }
        }
        else {
            // If se_parameter is not provided, set a default value
            this.se_selection = 0.4;
        }
        console.log(this.se_selection);
    };
    return AdaptivePREVICTest;
}());
//new instace of class to run constructur and start test
new AdaptivePREVICTest();
